import React from 'react'
import axios from 'axios'
import moment from 'moment'
import CardContainer from '../../components/CardContainer'
import { Table, Td, Th } from '../../components/Table'

const ScraperView = () => {
  const [scrapers, setScrapers] = React.useState([])
  React.useEffect(() => {
    async function fetchData() {
      const response = await axios('/api/scrapers/')
      setScrapers(response.data.scrapers)
    }
    fetchData()
  }, [])
  return (
    <CardContainer height='30vh' width='50%' margin='2rem auto' padding='2rem' justifyContent='center' alignItems='center'>
      <Table>
        <tr>
          <Th>ID</Th>
          <Th>Moneda</Th>
          <Th>Último valor leído</Th>
          <Th>Fecha última actualización</Th>
          <Th>Frecuencia</Th>
          <Th>Fecha de creación</Th>
        </tr>
        {scrapers.map(scraper => (
          <tr>
            <Td>{scraper.id}</Td>
            <Td>{scraper.currency}</Td>
            <Td>$ {scraper.value}</Td>
            <Td>{moment(scraper.value_updated_at).format("HH:mm:ss DD/MM/YY")}</Td>
            <Td>{scraper.frequency}</Td>
            <Td>{moment(scraper.created_at).format("HH:mm:ss DD/MM/YY")}</Td>
          </tr>
        ))}
      </Table>
    </CardContainer>
  )
}

export default ScraperView